import app from 'flarum/forum/app';
import { extend } from 'flarum/common/extend';
import LoginModal from 'flarum/forum/components/LoginModal';
extend(LoginModal.prototype, 'footer', function (items) {
  const customLink = 'https://findtheseven.com/forgot-password';
  // return <div>rtwarwa</div>
  return "TEST";
  items.replace('forgotPassword', m('a', {
    href: customLink,
    target: '_blank',
    rel: 'noopener noreferrer'
  }, app.translator.trans('core.forum.login.forgot_password_link')));
});